import MasterPage from '../components/Master'
import fetch from 'isomorphic-unfetch'
import Precio from '../components/Precio'
import Noticias from '../components/Noticias'



const Index = (props) => (



    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio
                    precio={props.precioBitcoin}
                />
            </div>
            <div className="col-md-8">
                <h2>Noticias sobre Bitcoin</h2>
                <Noticias
                    noticias={props.noticias}
                />
            </div>
            <div className="col-md-4">
                <h2>Proximos eventos Bitcoin</h2>
            </div>
        </div>
    </MasterPage>
);

Index.getInitialProps = async () => {

    const location = 'Mexico';
    const nombre = 'Bitcoin';
    const ordenar = 'date';
    const token = 'NPY7MA4BL6ZJU6TOIDST';

    const APICoin = 'https://api.coinmarketcap.com/v2/ticker/1/?CMC_PRO_API_KEY=fe231f8e-6d40-49c8-a22b-231123a7543a';
    const APINewsURL = 'https://newsapi.org/v2/everything?q=bitcoin&from=2019-05-28&sortBy=publishedAt&apiKey=fc4a6480075948dcb76fca1dd6db4d5a&language=es';
    const eventAPI = `https://www.eventbriteapi.com/v3/events/search/?q=${nombre}&location.address=${location}&sort_by=${ordenar}&token=${token}&locale=es_ES`


    const precio = await fetch(APICoin);
    const noticias = await fetch(APINewsURL);
    const eventos = await fetch(eventAPI);

    const resPrecio = await precio.json();
    const resNoticias = await noticias.json();
    const resEventos = await eventos.json();

    console.log(eventAPI);

    return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticias: resNoticias.articles,
    }
}

export default Index
